@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&family=Montserrat:ital,wght@0,400;0,700;1,400;1,500&display=swap);
body{margin:0;font-family:Karla sans-serif}h4 p{font-family:Karla sans-serif}.card{height:100%;display:flex}.card>.card-body{font-weight:normal;text-align:center;max-width:20rem;min-width:10rem;margin:0.1rem}.card .title{font-weight:bolder}.card>.details{display:flex;justify-content:space-between;margin:0.5rem;font-style:italic}.card>.card-grid-view{display:flex;justify-content:space-between;flex-wrap:wrap;margin-left:2rem;margin-top:1rem;width:75%}.navbar-a>a{color:rgba(255,255,255,0.5);padding:.5rem;text-decoration:none;background:transparent}.navbar-a>a:hover{text-decoration:none;color:#ffffff;background-color:transparent;-webkit-text-decoration-style:solid;text-decoration-style:solid}#boutonInstall{background-color:transparent;-webkit-text-decoration-style:solid;text-decoration-style:solid;border:solid gray;color:rgba(255,255,255,0.5)}#boutonInstall:hover{color:#ffffff}

